label {
  font-size: 1rem;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 91.8vh;
}

.form-box{
  width: 50%;
  border: solid 0.2rem #4582ec; 
  border-radius: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.btn-block{
  width: 50%;
  margin: 0 auto;
}

@media screen and (max-width: 991.98px) {
  .form-box{
    width: 60%;
  }
  
  .btn-block{
    width: 70%;
  }
}

@media screen and (max-width: 767.98px) {
  .form-box{
    width: 80%;
  }

}

@media screen and (max-width: 575.98px) {
  .login-title{
    font-size: 1.5rem;
  }

  .form-label{
    display: none;
  }
}

@media screen and (max-width: 296px) {
  .login-title{
    font-size: 1.3rem;
  }
}